.alert-enter {
  opacity: 0;
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, -webkit-transform 300ms;
  -o-transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition: opacity 1100ms, -webkit-transform 1300ms;
  transition: opacity 1100ms, -webkit-transform 1300ms;
  -o-transition: opacity 1100ms, transform 1300ms;
  transition: opacity 1100ms, transform 1300ms;
  transition: opacity 1100ms, transform 1300ms, -webkit-transform 1300ms;
}

.box{
  width: 600px;
  background-color:rgba(235, 157, 157, 0.728);

}

#btn{
  background-color: rgb(169, 104, 160);
  color:white;
}
#btn:hover{
color: black;
background-color:rgb(231, 138, 138);
}


